<template>
  <main class="login">

    <section class="page-title-section theme-bg">
      <div class="container">
        <div class="page-title">
          <h1>Sign In</h1>
          <ol class="breadcrumb justify-content-center">
              <li><router-link to="/">Home</router-link></li>
              <li class="active">Sign in</li>
          </ol>
        </div>
      </div>
    </section>

    <section>
      <div class="container container-large">
        <div class="row sm-p-30px-b">
          
          <div class="col-md-6 offset-md-3 col-lg-4 offset-lg-4 right-img md-m-30px-t">
            <div class="contact-form my-3">
              <img  class="user-avatar-cred" src="../../assets/static/img/user-icon.svg" alt="user sign in">
              <form v-on:submit.prevent="onSubmit" class="contactform py-3">
                <div class="row">
                  <div class="col-md-12">
                    <div class="form-group">
                      <input v-model="user.email" type="email" placeholder="Email" class="form-control" required>
                      <div class="invalid-feedback block" v-if="errors.email">{{errors.email}}</div>
                    </div>
                  </div>
                  <div class="col-md-12">
                    <div class="form-group">
                      <input v-model="user.password" type="password" placeholder="Password" class="form-control" required>
                      <div class="invalid-feedback block" v-if="errors.password">{{errors.password}}</div>
                    </div>
                  </div>
                  <div class="col-md-12">
                    <div class="send">
                      <button class="btn btn-primary" type="submit" name="send"> Login</button>
                    </div>
                    <div class="clearfix"></div> <br/>
                      <p class="gray-dark"><router-link to="forgot" >Forgot Password</router-link></p> 
                      <p class="gray-dark">Don't have account? <router-link to="register">Register</router-link> </p>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div> 
      </div> 
    </section>
    <FlashMessage></FlashMessage>
  </main>
</template>

<script>
    import * as auth from '../../services/auth_service'
    export default {
      name: 'login',
      data() {
          return {
            user: {
              email: '',
              password: '',
            },

            errors: {}
          }
      },
      created() {
        if (this.$store.state.commonMessage && this.$store.state.commonMessage === 'registration') {
          this.flashMessage.info({
            title: 'Registration',
            message: 'Thank you! You have been successfully registered, Try to login now',
            time: 5000,
            position: 'right top',
            icon: '/img/user-dummy.52069854.jpg'
          });
          this.$store.state.commonMessage = null;
        }
      },
      methods: {
        onSubmit: async function() {
          await auth.login(this.user)
          .then(response => {
            if (this.$store.state.commonMessage && this.$store.state.commonMessage === 'registration') {
              this.$store.state.commonMessage = 'after_registration';
            } else {
              this.$store.state.commonMessage = 'normal_login';
            }
            if (this.$store.state.userAccountType == 1315) {
              this.$router.push({ name: 'meter-owner' });
            } else if(this.$store.state.userAccountType == 51) {
              this.$router.push({ name: 'estate-admin' });
            } else {
              alert('Admin is not yet been created!');
            }
          })
          .catch(error => {
              if (error.response.data.errors.message) {
                  this.errors = {};
                  this.flashMessage.error({
                      title: 'Error',
                      message: error.response.data.errors.message,
                      time: 7000,
                      icon: '/img/user-dummy.52069854.jpg'
                  });
              } else {
                  this.errors = error.response.data.errors;
              }
          }); 
        }
      }
    }
</script>
